@import "./src/fonts/fonts";


.program {
  margin: 70px 0;
  @media screen and(max-width: 1023px) {
    margin: 0;
  }

  .programTitle {
    font-weight: 700;
    font-size: 26px;
    line-height: 31.2px;
    @media screen and(max-width: 570px) {
      margin-top: 35px;
      margin-bottom: 27px;
      font-weight: 700;
      font-size: 22px;
      line-height: 26.4px;
    }
  }
}

